.card {
  width: 200px;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.card__img {
  display: block;
  margin: 0 auto;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
  color: var(--main-color);
  margin-bottom: 4px;
}

.card__code {
  display: block;
  margin: 0;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin-bottom: 16px;
}

.card__review {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.card__reviews {
  margin: 0;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: var(--main-color);
}

.card__price {
  display: flex;
  justify-content: space-between;
}

.card__price-text {
  margin: 0;
  font-weight: 400;
  color: #616070;
  margin-bottom: 16px;
}

.card__price-value {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  color: var(--main-color);
}

.card__link {
  display: block;
  width: 166px;
  height: 40px;
  text-align: center;
  background-color: var(--additional-color);
  color: #fff;
  border-radius: 5px;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 40px;
  border: 1px solid rgba(0, 172, 220, 0);
}

.card__link:hover {
  background: transparent;
  border: 1px solid rgba(0, 172, 220, 1);
  color: var(--additional-color);
}
